<template>
  <div>
    <Loadings v-if="data ==null"></Loadings>
    <div class="container" v-else>
      <!-- <div class="caption">过期未收款</div> -->
      <Empty v-if=" data.length == 0"></Empty>
      <div class="list">
        <div
    
          class="item"
          v-for="(item, index) in data"
          :key="index"
        >
          <div class="info">
            <div class="company flex-sb">
              {{item.company}}
              <span class="time">过期{{item.overTime}}天</span>
            </div>
            <div class="name">地址:{{item.address}}</div>
            <div class="name">
              姓名:{{item.name}}
              <span style="padding-left:15px;">电话:{{item.phone}}</span>
            </div>
            <div class="name">客户类型:{{item.status}}</div> 
 
            <div class="name">合同号:{{item.contract_no}}</div>
            <div class="name">发货日期:{{formatDate(item.invoice_time)}}</div>
            <div class="name">业务员:{{item.optioner}}</div> 
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { payOverdue } from "@api/public";
import Empty from "@components/Empty";
import Loadings from "@components/Loadings";
import { Icon } from "vant";
export default {
  name: "OverduePay",
  components: {
    Empty,
    Loadings,
    [Icon.name]: Icon,
  },
  props: {},
  data: function () {
    return {
      data: [],
    };
  },
  created() {

  },
  mounted: function () {},
  methods: {
    formatDate: function (value) {
      let date = new Date(value * 1000);
      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? "0" + MM : MM;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;

      return y + "-" + MM + "-" + d + " ";
    },
  },
};
</script>
